  font-size: 100%;
  font-style: inherit;
  font-variant: inherit;
  font-weight: inherit;
  line-height: inherit;
  vertical-align: baseline;
  border: 0;
  margin: 0;
  padding: 0;
  font-family: inherit;
}

article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after, q:before, q:after {
  content: "";
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

.page {
  scroll-behavior: smooth;
  font-family: Cinzel, serif;
}

.page__body {
  margin: 0;
}

.page__body--with-menu {
  overflow: hidden;
}

.page__header {
  margin-bottom: 100px;
}

@media (width >= 744px) {
  .page__header {
    margin-bottom: 140px;
  }
}

.page__menu {
  transition: transform .5s;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  transform: translateX(-100%);
}

.page__menu:target {
  transform: translateX(0);
}

.page__section {
  margin-bottom: 100px;
}

@media (width >= 744px) {
  .page__section {
    margin-bottom: 140px;
  }
}

