  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  width: 18px;
  height: 18px;
  display: block;
  position: relative;
}

.icon__tooltip {
  text-align: right;
  opacity: 0;
  pointer-events: none;
  width: max-content;
  padding-right: 20px;
  transition: opacity .5s;
  position: absolute;
  transform: translate(-100%, 5%);
}

.icon:hover + .icon__tooltip {
  opacity: 1;
  pointer-events: all;
}

